#user-head, #user-background {
  width: 100%;
  border-radius: 50%;
}

label {
  margin-bottom: 0;
  cursor: pointer;
  margin-left: 25%;
  width: 50%;
}

#upload-head, #upload-background {
  display: none;
}

.user-center * {
  font-family: 'Monaco', 'Lucida Console', monospace;
}

.glyphicon {
  font-family: 'Glyphicons Halflings';
}

#records-board, #articles-board {
  .glyphicon-remove {
    display: none;
    cursor: pointer;
  }
}

.thumbnail {
  color: #fff;
  img {
    width: 30%;
    margin: 20px auto 20px 35%;
    border-radius: 50%;
  }
  .caption {
    color: #fff;
    small {
      color: #fafafa;
    }
    h5 {
      a {
        color: #fff !important;
      }
    }
  }
}

@media screen and (min-width: 992px) {
  .thumbnail {
    img {
      width: 90%;
      margin: auto auto auto 5%;
      border-radius: 50%;
    }
  }
}